import ProtectedRoute from '../../components/ProtectedRoute';
import { useAuth } from '../../context/authContext';

export default function AdminDashboardPage() {
  const { logout } = useAuth();
  return (
    <ProtectedRoute allowedRoles={["admin"]}>
      <main className="p-8">
        <div className="flex justify-between items-center mb-4">
          <h1 className="text-3xl font-bold">Dashboard Administrador</h1>
          <button
            className="bg-red-500 text-white px-4 py-2 rounded"
            onClick={logout}
          >
            Cerrar sesión
          </button>
        </div>
        <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
          <div className="bg-white rounded shadow p-6">
            <h2 className="text-xl font-semibold mb-2">Usuarios</h2>
            <p>Gestión de usuarios, roles y contraseñas.</p>
          </div>
          <div className="bg-white rounded shadow p-6">
            <h2 className="text-xl font-semibold mb-2">Grados y Secciones</h2>
            <p>Gestión de grados, secciones y asignaciones.</p>
          </div>
          <div className="bg-white rounded shadow p-6">
            <h2 className="text-xl font-semibold mb-2">Asignaturas</h2>
            <p>Gestión de asignaturas y asignaciones.</p>
          </div>
        </div>
        <div className="mt-8">
          <h2 className="text-xl font-semibold mb-2">Métricas clave</h2>
          <ul className="list-disc ml-6">
            <li>Total de usuarios</li>
            <li>Total de grados/secciones</li>
            <li>Total de asignaturas</li>
            <li>Último respaldo</li>
          </ul>
        </div>
      </main>
    </ProtectedRoute>
  );
}
